import { supabase } from "@/lib/supabase";
import { notFound } from "next/navigation";
import StoreFront from "@/components/StoreFront"; // We will fix this component next
import ViewTracker from "@/components/ViewTracker";
import { Metadata, ResolvingMetadata } from "next"; 

interface PageProps {
  params: Promise<{ slug: string }>;
}

export async function generateMetadata(
  { params }: PageProps,
  parent: ResolvingMetadata
): Promise<Metadata> {
  const resolvedParams = await params;
  const slug = resolvedParams.slug;

  const { data: store } = await supabase.from("stores").select("name, description, logo_url, cover_image_url").eq("slug", slug).single();

  if (!store) {
    return { title: "Store Not Found" };
  }

  return {
    title: `${store.name} | StoreLink`,
    description: store.description || `Shop at ${store.name}`,
    openGraph: {
      title: store.name,
      description: store.description || "Check out my store on StoreLink!",
      images: [store.cover_image_url || store.logo_url || "/default-cover.jpg"],
    },
  };
}

export default async function StorePage({ params }: PageProps) {
  const resolvedParams = await params;
  const slug = resolvedParams.slug;

  const { data: store } = await supabase
    .from("stores")
    .select("*")
    .eq("slug", slug)
    .single();

  if (!store) return notFound();

  const { data: products } = await supabase
    .from("storefront_products") 
    .select("*, categories(name)") 
    .eq("store_id", store.id)
    .order("created_at", { ascending: false });

  const { data: categories } = await supabase
    .from("categories")
    .select("id, name")
    .eq("store_id", store.id)
    .order("name", { ascending: true });

  return (
    <>
      <ViewTracker storeId={store.id} />
      <StoreFront 
        store={store} 
        products={products || []} 
        categories={categories || []} 
      />
    </>
  );
}